import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { AxiosMasterGet } from "../Axios";
import { renderField, renderFieldSelect } from "../renderField";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "id-ID",
});
class CetakBarcodeHead extends Component {
  constructor(props) {
    super(props);
    this.state = {
      list_barang: [],
      non_aksesoris: "col-lg-12 row",
      aksesoris: "col-lg-12 row d-none",
    };
  }

  componentDidMount() {
    AxiosMasterGet("jenis/get/all-no-image").then((res) =>
      this.setState({
        list_barang: res.data,
      })
    );
  }

  handleKelompok(data) {
    if (data.split("|")[1] === "AKSESORIS") {
      this.setState({
        non_aksesoris: "col-lg-12 row d-none",
        aksesoris: "col-lg-12 row ",
      });
    } else {
      this.setState({
        non_aksesoris: "col-lg-12 row ",
        aksesoris: "col-lg-12 row d-none ",
      });
    }
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-12">
            <h3>Barcode 1</h3>
            <hr />
            <div className="row">
              <div className="col-lg-6">
                <Field
                  name="kode_barang"
                  component={renderFieldSelect}
                  options={this.state.list_barang.map((list) => {
                    let value = {
                      value: list.kode_jenis + "|" + list.kode_kelompok,
                      name: list.kode_jenis,
                    };
                    return value;
                  })}
                  type="text"
                  label="Kode Model"
                  placeholder="Masukan Kode Model"
                  onChange={(data) => this.handleKelompok(data)}
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="sub_kategori"
                  component={renderField}
                  type="text"
                  label="Sub Kategori"
                  placeholder="Masukan Sub Kategori"
                />
              </div>
              <div className={this.state.non_aksesoris}>
                <div className="col-lg-3">
                  <Field
                    name="pkg"
                    component={renderField}
                    type="text"
                    label="PKG (Gr)"
                    placeholder="Masukan Pkg"
                    onChange={() => this.props.change("gw", this.props.gw)}
                    onBlur={() => this.props.change("gw", this.props.gw)}
                    step="0.01"
                  />
                </div>
                <div className="col-lg-3">
                  <Field
                    name="nw"
                    component={renderField}
                    type="text"
                    label="NW (Gr)"
                    placeholder="Masukan NW"
                    onChange={() => this.props.change("gw", this.props.gw)}
                    onBlur={() => this.props.change("gw", this.props.gw)}
                    step="0.01"
                  />
                </div>
                <div className="col-lg-3">
                  <Field
                    name="gw"
                    component={renderField}
                    type="text"
                    label="GW (Gr)"
                    placeholder="Masukan GW"
                  />
                </div>
                <div className="col-lg-3">
                  <Field
                    name="tukaran"
                    component={renderField}
                    type="text"
                    label="Kode Tukar (%)"
                    placeholder="Masukan Kode Tukar (%)"
                  />
                </div>
                <div className="col-lg-5">
                  <Field
                    name="ongkos"
                    component={renderField}
                    type="telp"
                    label="Ongkos (Rp)"
                    placeholder="Masukan Ongkos (Rp)"
                    {...currencyMask}
                  />
                </div>
              </div>
              <div className={this.state.aksesoris}>
                <div className="col-lg-3">
                  <Field
                    name="qty"
                    component={renderField}
                    type="number"
                    label="Qty"
                    placeholder="Masukan Qty"
                  />
                </div>
                <div className="col-lg-5">
                  <Field
                    name="harga_jual"
                    component={renderField}
                    type="telp"
                    label="Harga Jual"
                    placeholder="Masukan Harga Jual"
                    {...currencyMask}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="col-lg-12">
          <div className="text-right">
            <button className="btn btn-primary">
              <i className="fa fa-print mr-2"></i> Cetak
            </button>
          </div>
        </div>
      </form>
    );
  }
}

CetakBarcodeHead = reduxForm({
  form: "CetakBarcodeHead",
  enableReinitialize: true,
})(CetakBarcodeHead);
const selector = formValueSelector("CetakBarcodeHead");
export default connect((state) => {
  const { pkg, nw, pkg2, nw2 } = selector(state, "pkg", "nw", "pkg2", "nw2");
  return {
    gw: parseFloat(pkg || 0) + parseFloat(nw || 0),
    gw2: parseFloat(pkg2 || 0) + parseFloat(nw2 || 0),
  };
})(CetakBarcodeHead);
